.head {
    height: 8vh;
    padding: 10px 0;
}

.head .logo {
    height: 8vh;
    width: 10%;
    float: left;
    border-right: 1px solid grey;
    text-align: center;
}
.head .logo img {
    height: 8vh;
}

@media(max-width: 1024px) {
    .head .logo img {
        height: 4vh;
    } 

    .head .logo {
        height: 5vh;
    } 

    .head {
        height: 5vh;
    }
}