.menu {
    float: left;
    width: 89.9%;
    height: 8vh;
}

.menu ul li {
    list-style: none;
    float: left;
    width: 24%;
    height: 100%;
    text-align: center;
    font-size: 20px;
    border-right: 2px dashed grey;
}

.menu ul li:nth-last-child(1){
    border-right: none;
}

.menu ul li a {
    text-transform: uppercase;
    text-decoration: none;
    color: darkgray;
    padding: 0 15px;
}

.menu ul li a:hover {
    color: #C5CC22;
}


.menu ul li a.active {
    color: #F7FF44;
}


@media(max-width: 1024px) {
    .menu ul li {
        font-size: 12px;
        border-right: 1px dashed grey;
    }

    .menu ul {
        position: absolute;
        left: 7px;
    }

    .menu {
        width: 89.7%;
    }

    .menu ul li:nth-child(3) {
        width: 27.5%;
    }

    .menu ul li:nth-child(1), .menu li:nth-child(2) {
        width: 21%;
    }
}