body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: yellow;
    background-color: rgb(68,67,70);
    min-height: 100vh;
}

header {
    width: 100%;
}

main {
    width: 100%;
}

footer {
    width: 100%;
    background-color: darkgoldenrod;
}
.head {
    height: 8vh;
    padding: 10px 0;
}

.head .logo {
    height: 8vh;
    width: 10%;
    float: left;
    border-right: 1px solid grey;
    text-align: center;
}
.head .logo img {
    height: 8vh;
}

@media(max-width: 1024px) {
    .head .logo img {
        height: 4vh;
    } 

    .head .logo {
        height: 5vh;
    } 

    .head {
        height: 5vh;
    }
}
.menu {
    float: left;
    width: 89.9%;
    height: 8vh;
}

.menu ul li {
    list-style: none;
    float: left;
    width: 24%;
    height: 100%;
    text-align: center;
    font-size: 20px;
    border-right: 2px dashed grey;
}

.menu ul li:nth-last-child(1){
    border-right: none;
}

.menu ul li a {
    text-transform: uppercase;
    text-decoration: none;
    color: darkgray;
    padding: 0 15px;
}

.menu ul li a:hover {
    color: #C5CC22;
}


.menu ul li a.active {
    color: #F7FF44;
}


@media(max-width: 1024px) {
    .menu ul li {
        font-size: 12px;
        border-right: 1px dashed grey;
    }

    .menu ul {
        position: absolute;
        left: 7px;
    }

    .menu {
        width: 89.7%;
    }

    .menu ul li:nth-child(3) {
        width: 27.5%;
    }

    .menu ul li:nth-child(1), .menu li:nth-child(2) {
        width: 21%;
    }
}
.foot {
    position: fixed;
    bottom: 0;
    height: 3vh;
    width: 100%;
    background-color: black;
    text-align: center;
}




.content {
    text-indent: 30px;
    min-height: 82.5vh;
    padding: 20px 100px 10px 100px;
    text-align: justify;
}

h2 {
    letter-spacing: 3px;
}

.sent {
    position: relative;
    letter-spacing: 2px;
    text-align: center;
    left: 30px;
}

.sign {
    position: absolute;
    right: 100px;
}

.content .link {
    text-decoration: none;
    color:rgba(251, 255, 0, 0.479);
}

.content .link:hover {
    color:rgba(251, 255, 0, 0.479);
    color: rgb(250, 253, 37);
}

@media(max-width: 1024px) {
    .content {
        padding: 20px 30px 10px 20px;
        font-size: 14px;
        min-height: 82vh;
    }
    
    h2 {
        font-size: 20px;
    }
}
span {
    display: inline-block;
    padding-bottom: 40px;
}

form {
    text-align: center;
}

form label {
    display: block;
    padding: 25px 0;

}

form label:nth-child(2) {
    position: relative;
    left: -30px;
}

form label input, form label textarea  {
    background-color: lightgray;
    border-radius: 10px;
}

form label span{
    vertical-align: top;
}


form label input:nth-child(1):focus {
    background-color:gray;
    color: yellow;
}

form label textarea:focus {
    background-color:gray;
    color: yellow;
}

form label input:nth-child(3):focus {
    background-color: gray;
    color: yellow;
}

form button {
    border-radius: 10px;
    width: 10em;
    position: relative;
    left: 35px;
    background-color:rgb(0, 0, 0);
    color: yellow;
    border: 1px solid lightgray;
}

form button:hover {
    cursor: pointer;
    background-color: gray;
}

.content ul{
    text-indent: 0;
    list-style-type: none;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.project {
    color: yellow;
    text-decoration: none;
    display: inline-block;
    transition: .3s;
}

.project:hover {
    letter-spacing: 10px;
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}
